import { IPhone } from './../../../shared/interface/identity/phone-interface';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ViewChild } from '@angular/core';
import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { CompanyDto } from 'src/app/shared/dtos/identity/company-dto';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';
import { ContactDetailComponent } from 'src/app/shared/components/contact-detail/contact-detail.component';

@Component({
  selector: 'app-create-edit-company',
  templateUrl: './create-edit-company.component.html',
  styleUrls: ['./create-edit-company.component.scss']
})
export class CreateEditCompanyComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;
  @ViewChild('contactDetail', { static: true }) contactDetail: ContactDetailComponent;

  company: CompanyDto = new CompanyDto();
  phone: IPhone = {} as IPhone;
  saving=false;
  itemList=[];
  _id=null;
  isNo=false;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Company No","field":"comp_no"},
    {"title":"Title","field":"comp_title"},
    {"title":"Short","field":"comp_short"}
  ];

  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) }

  ngOnInit() {
    this.phone.type='Email';
    this.company.comp_phones= [];
  }
  getData(){
    this.itemList= [];
    this._processDataService.getData("/api/companies").subscribe(response=>{
      if (response.status === 1) {
        debugger
        this.itemList = response.data;
        this.showDataModal.show(this.tableHeaders,this.itemList);
      }
    })
  }
  getDataById(event){
    this._processDataService.getDataById("/api/companies/" + event._id).subscribe(response=>{
      if (response.status === 1) {
        this.company = response.data;
        this.company.comp_phones = response.data.comp_phones
        this._id=event._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/companies/" + this.company.comp_no).subscribe(response=>{
      if(response.status === 1){
        this.company = response.data;
        this.company.comp_phones = response.data.comp_phones
      }else{
        this.notify.error('Search item not found','Not Found');
        this.reset();
      }
    })
  }
  getNextNo(){
    this._processDataService.getData("/api/nextNumber/company").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.company.comp_no =response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  save(saveForm:NgForm):void{
    debugger
    this.saving=true;
    let url_ = "/api/companies/";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/companies/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }

  create(url_: string, saveForm:NgForm): void{
    this.company.comp_phones=this.contactDetail.contacts;
    this.company.comp_city= "609a786c944ebf09cc34c8f9";
    this.company.comp_owner= "609bc492577f0418f8c37d34";
    this.company.comp_manager= "609bc492577f0418f8c37d34";
    this.company.comp_ho= "609a7fa71737dc36d8be4664";
    this._processDataService.create(url_,this.company).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status == 1) {
        this.company.comp_no = response.data.item_no + 1;
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.company).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status == 1) {
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/companies/" + this._id).subscribe(result => {
          this.notify.success('Deleted Successfully.', 'Deleted!');
          this.reset();
        });
      }
    });
  }

  reset():void{
    this.company=new CompanyDto();
    this._id=null;
    this.isNo = false;
  }

}


