import { state } from '@angular/animations';
import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { LocationDto } from 'src/app/shared/dtos/setup/location-dto';
import { ITypeheadFormatter } from 'src/app/shared/interface/identity/typehead-formatter-interface';
import { SetupService } from 'src/app/shared/services/setup.service';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-location',
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.scss']
})
export class LocationComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;

  typeList=['Country', 'State','City'];
  countries:ITypeheadFormatter[]=[];
  states:ITypeheadFormatter[]=[];
  location: LocationDto = new LocationDto();
  saving=false;
  locationList=[];
  country:{id:string,name:string};
  state:{id:string,name:string};
  showState=false;
  showCountry=false;
  _id=null;
  isNo = false;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Location No","field":"loc_no"},
    {"title":"Location Title","field":"loc_title"},
    {"title":"Location Type","field":"loc_type"}
  ];
  
  constructor(
    injector: Injector,
    private _processDataService:SetupService
  ) {super(injector)
    this.location.loc_active=true; 
  }

  getData(){
    this.locationList= [];
    this._processDataService.getData("/api/locations").subscribe(response=>{
      if (response.status === 1) {
        this.locationList = response.data;
        this.showDataModal.show(this.tableHeaders,this.locationList);
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  getDataById(event){
    this._processDataService.getDataById("/api/locations/id/" + event._id).subscribe(response=>{
      if (response.status === 1) {
        this.location = response.data;
        this._id=event._id;
        this.isNo = true;
        this.getLocation(this.location.loc_type);
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/locations/no/" + this.location.loc_no).subscribe(response=>{
      if(response.status === 1){
        this.location = response.data;
        this._id = response.data._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
        this.reset();
      }
    })
  }

  getNextNo(){
    this._processDataService.getData("/api/nextNumber/Location").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.location.loc_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  save(saveForm:NgForm):void{
    debugger
    switch (this.location.loc_type) {
      case 'State':
        this.location.loc_parent=this.country.id;
        break;
      case 'City':
        this.location.loc_parent=this.state.id;
        break;
    }
    this.location.loc_geo={lat:0,long:0};
    this.saving=true;
    let url_ = "/api/locations";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/locations/id/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }

  create(url_: string, saveForm:NgForm): void{
    this._processDataService.create(url_,this.location).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      debugger
      if (response.status == 1) {
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.location).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/locations/" + this._id).subscribe(result => {
          if (result.status === 1) {
            this.notify.success('Deleted Successfully.', 'Deleted!');
            this.reset();
          }else{
            this.message.fire("Error", result.message, "error");
          }
        });
      }
    });
  }

  reset():void{
    this.location=new LocationDto();
    this._id=null;
    this.isNo = false;
    this.location.loc_active=true;
    this.saving=false;
  }
  setTypeheadList(param){
    switch (param) {
        case 'type':
        this.typeheadlist=this.typeList
        break;
        case 'country':
        this.typeheadlist=this.countries
        break;
        case 'state':
        this.typeheadlist=this.states
        break;
      
    } 
  }
  getLocation(param){
    switch (param) {
      case 'State':
        this.showState=false;
        this.getLocationData('country');
        break;
        case 'City':
          this.showCountry=false;
          this.getLocationData('state');
          break;
      default:
        this.showState=false; 
        this.showCountry=false;
        break;
    }

  }
  getLocationData(param){
    debugger
    this._processDataService.getData("/api/locations/"+param).subscribe(response=>{
      debugger
      if (response.status === 1) {
        this.states=[];
        this.countries=[];
        var itemList=[];
        response.data.forEach(loc => {
            var item = {id: loc._id,name:loc.loc_title};
            itemList.push(item);
        });
        switch (param) {
          case 'country':
            this.countries=itemList;
            this.showCountry=true;
          break;
          case 'state':
            this.states = itemList;
            this.showState=true;
            
          break;
        }
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
}
