import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LocationComponent } from './location/location.component';
import { CreateEditCompanyComponent } from './company/create-edit-company.component';
import { CreateEditOfficeComponent } from './office/create-edit-office.component';
import { CreateEditStaffComponent } from './staff/create-edit-staff.component';


const routes: Routes = [
  {
    path:'',
    children:[
      {
        path:'location',
        component:LocationComponent
      },
      {
        path:'company',
        component:CreateEditCompanyComponent
      },
      {
        path:'office',
        component:CreateEditOfficeComponent
      },
      {
        path:'staff',
        component:CreateEditStaffComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SetupRoutingModule { }
