import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { BilltiesDto } from 'src/app/shared/dtos/identity/bilities-dto';
import { IItemPackListDto } from 'src/app/shared/interface/identity/billties-interface';
import { IItemPackDto } from 'src/app/shared/interface/identity/itempack-interface';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-create-edit-bilty',
  templateUrl: './create-edit-bilty.component.html',
  styleUrls: ['./create-edit-bilty.component.scss']
})
export class CreateEditBiltyComponent extends AppComponentBase implements OnInit {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;

  btModelList=['Retail', 'LCL', 'FCL'];
  btModeList=['Regular', 'Godown'];
  btTypeList=['Book', 'Deliver', 'Return'];
  btPaymodeList=['To-Pay', 'Paid'];
  billtiesDtoList: BilltiesDto[]=[];
  billtiesDto:BilltiesDto = new BilltiesDto();
  itemPackListDto : IItemPackListDto[]=[];
  itemPackDto: IItemPackListDto={} as IItemPackListDto;
  saving=false; 
  itemList=[];
  _id=null;
  isNo = false;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Doc No","field":"bt_no"},
    {"title":"Model","field":"bt_model"},
    {"title":"Mode","field":"bt_mode"}
  ];
  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) }

  ngOnInit() {
    this.insertBiltyItem();
  }
  getData(){ 
    this.billtiesDtoList= [];
    this._processDataService.getData("/api/billties").subscribe(response=>{
      if (response.status === 1) {
        this.billtiesDtoList = response.data;
        this.showDataModal.show(this.tableHeaders,this.itemList);
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataById(event){
    this._processDataService.getDataById("/api/billties/" + event._id).subscribe(response=>{
      
      if (response.status === 1) {
        this.billtiesDto = response.data;
        this.itemPackListDto=this.billtiesDto.bt_items;
        this._id=event._id;      
      }
      else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/billties/" + this.billtiesDto.bt_no).subscribe(response=>{
      if(response.status === 1){
        this.billtiesDto = response.data;
        this.isNo = true;
        this.itemPackListDto=this.billtiesDto.bt_items;
      //this._id=event._id;
    }else{
      this.message.fire("Error", response.message, "error");
      this.reset();
    }
  });
}
getNextNo(){
  this._processDataService.getData("/api/nextNumber/bilty").subscribe(response=>{
    if (response.status === 1) {
      this.reset();
      this.billtiesDto.bt_no = response.data.value;
      this.isNo = true;
    }else{
      this.message.fire("Error", response.message, "error");
    }
  })
}

  save(saveForm:NgForm):void{
    debugger
    this.saving=true;
    let url_ = "/api/billties/";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/billties/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }

  create(url_: string, saveForm:NgForm): void{
    this.billtiesDto.bt_items=this.itemPackListDto;
    this._processDataService.create(url_,this.billtiesDto).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if(response.status === 1){
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.billtiesDto).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if(response.status === 1){
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
        
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
//Add dynamic grid row
insertBiltyItem(){
  var itempack={} as IItemPackDto; 
  this.itemPackListDto.push({
    itempack:itempack
  });
  console.log(this.itemPackListDto);
}
//Delete dynamic grid row
deleteBiltyItem(index){
  this.itemPackListDto.splice(index,1);
}
setTypeheadList(param){
  switch (param) {
      case 'btModel':
      this.typeheadlist=this.btModelList;
      break;
      case 'btmode':
      this.typeheadlist=this.btModeList;
      break;
      case 'btType':
      this.typeheadlist=this.btTypeList;
      break;
      case 'btPaymode':
      this.typeheadlist=this.btPaymodeList;
      break;
  } 
}
delete(): void {
  this.message.fire({
    title: 'Are you sure?',
    html: 'You are about to delete 1 record permanently.',
    icon: 'warning',
    showCancelButton: true,
    confirmButtonText: 'Delete',
    cancelButtonText: 'Cancel'
  }).then((result) => {
    if (result.value) {
      this._processDataService.delete("/api/bilty/" + this._id).subscribe(result => {
        if (result.status === 1) {
          this.notify.success('Deleted Successfully.', 'Deleted!');
          this.reset();
        }else{
          this.message.fire("Error", result.message, "error");
        }
      });
    }
  });
}
reset():void{
  this.billtiesDto=new BilltiesDto();
  this._id=null;
  this.isNo = false;
}
}
