import { IPhone } from './../../../shared/interface/identity/phone-interface';
import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { VendorDto } from 'src/app/shared/dtos/identity/vendor-dto';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';
import { IContactDetailDto } from 'src/app/shared/interface/identity/contactdetail-interface';
import { IOfficeDetailDto } from 'src/app/shared/interface/identity/officedetail-interface';
import { IVendorListContact, IVendorListOffice } from 'src/app/shared/interface/identity/vendor-interface';

@Component({
  selector: 'app-vendor',
  templateUrl: './create-edit-vendor.component.html',
  styleUrls: ['./create-edit-vendor.component.scss']
})
export class CreateEditVendorComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;

  vendorTypeList= ['Individual','Company','Open-Market'];
  vendorTermList= [ 'No-Contract', 'On-Contract'];
  vendorserviceList= [ 'Labor', 'Transport', 'Forwarding', 'Booking-Agent', 'Port', 'Shipping-Line', 'Media', 'Food',
    'Import', 'Export', 'Maintenance', 'Media', 'Marketing' ];
  vendorDesignation=['Owner', 'Chairman', 'MD', 'President', 'EVP', 'SVP', 'VP', 'GM', 'Director', 'Senior Manager', 'Manager', 'Executive', 'Accountant', 'Clerk', 'Driver', 'Assistant', 'Loader', 'Labor'];
vendor: VendorDto= new VendorDto();
contactDetail:IContactDetailDto={} as IContactDetailDto;
officeDetail:IOfficeDetailDto={} as IOfficeDetailDto;
vendorContact:IVendorListContact={} as IVendorListContact;
vendorOffice:IVendorListOffice={} as IVendorListOffice;

phone:IPhone={} as IPhone;
vend_phone:IPhone={} as IPhone;
vend_contact:IPhone={} as IPhone;
off_contact:IPhone={} as IPhone;

VendorList=[];
saving=false;
_id=null;
isNo = false;
tableHeaders=[
  {"title":"Id","field":"vend_no","hide":true},
  {"title":"Title","field":"vend_title"},
  {"title":"Vendor short","field":"vend_short"},
  {"title":"Type","field":"vend_type"}
];
  constructor( injector: Injector,
    private _processDataService:IdentityService
    ) {super(injector) }

  ngOnInit() {
    this.vendor.vend_phones=[];
    this.contactDetail.cust_phones=[];
    this.contactDetail.contacts=[];
    this.officeDetail.phones=[];
    this.vendor.vend_contacts=[];
    this.vendor.vend_offices=[];
    this.vendorContact.contact={} as IContactDetailDto;
    this.vendorOffice.office={} as IOfficeDetailDto;
    }
  getData(){ 
    this.VendorList= [];
    this._processDataService.getData("/api/vendors").subscribe(response=>{
      debugger
      if (response.message = "Success") {
        this.VendorList = response.data;
        this.showDataModal.show(this.tableHeaders,this.VendorList);
      }
    })
  }
  getDataById(event){
    this._processDataService.getDataById("/api/vendors/" + event._id).subscribe(response=>{
      this.vendor = response.data;
      this._id=event._id;
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/vendor/no/" + this.vendor.vend_no).subscribe(response=>{
      if(response.status === 1){
        this.vendor = response.data;
        this._id = response.data._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
        this.reset();
      }
    })
  }

  getNextNo(){
    this._processDataService.getData("/api/nextNumber/vendor").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.vendor.vend_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  save(saveForm:NgForm):void{
    this.saving=true;
    debugger
    this.vendor.vend_phones.push(this.phone);
    this.contactDetail.cust_phones.push(this.vend_phone);
    this.contactDetail.contacts.push(this.vend_contact);
    this.officeDetail.phones.push(this.off_contact);
    this.vendorContact.contact = this.contactDetail;
    this.contactDetail.city="609a786c944ebf09cc34c8f9";
    this.vendor.vend_contacts.push(this.vendorContact);
    this.vendorOffice.office=this.officeDetail;
    this.vendorOffice.office.city="609a786c944ebf09cc34c8f9";
    this.vendor.vend_offices.push(this.vendorOffice)
    this.vendor.vend_city="609a786c944ebf09cc34c8f9";
    this.vendor.vend_comp="609c01f4c645ce303c655ad2";
    this.vendor.vend_ud=true;
    //this.vendor.vend_offices.push();
    let url_ = "/api/vendors/";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/vendors//id/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }
  create(url_: string, saveForm:NgForm): void{
    this._processDataService.create(url_,this.vendor).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      debugger
      if (response.status == 1) {
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.vendor).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/vendors/" + this._id).subscribe(result => {
          this.notify.success('Deleted Successfully.', 'Deleted!');
          this.vendor=new VendorDto();
          this._id=null;
        });
      }
    });
  }
  setTypeheadList(param){
    switch (param) {
        case 'type':
        this.typeheadlist=this.vendorTypeList;
        break;
        case 'term':
        this.typeheadlist=this.vendorTermList;
        break;
        case 'service':
        this.typeheadlist=this.vendorserviceList;
        break;
        case 'desig':
        this.typeheadlist=this.vendorDesignation;
        break;
      
    } 
  }
  reset():void{
    this.vendor=new VendorDto();
    this._id=null;
    this.isNo = false;
  }

}
