import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ViewChild } from '@angular/core';
import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';
import { merge, Observable, OperatorFunction, Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter, finalize, map } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { VoucherDto } from 'src/app/shared/dtos/identity/voucher-dto';
import { ILoaderInfoDto } from 'src/app/shared/interface/identity/loaderInfo-interface';
import { IVehicleInfoDto } from 'src/app/shared/interface/identity/vehicleinfo-interface';
import { IVehicleInfoListDto, IVehicleLoaderListDto } from 'src/app/shared/interface/identity/voucher-interface';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-create-edit-voucher',
  templateUrl: './create-edit-voucher.component.html',
  styleUrls: ['./create-edit-voucher.component.scss']
})
export class CreateEditVoucherComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;

  vcModelList=['Retail', 'LCL', 'FCL'];
  vcTypeList= ['Book', 'Deliver', 'Return'];
  vcPaymode= ['To-Pay', 'Paid'];
  Voucher: VoucherDto = new VoucherDto();
  
  vehicleInfoListDto: IVehicleInfoListDto={} as IVehicleInfoListDto;
  vehicleInfoDto:IVehicleInfoDto={} as IVehicleInfoDto;

  vehicleLoaderListDto:IVehicleLoaderListDto={} as IVehicleLoaderListDto;
  loaderInfoDto: ILoaderInfoDto={} as ILoaderInfoDto;
  saving=false;
  voucherList=[];
  uoms=[];
  _id=null;
  isNo = false;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Voucher No","field":"voucher_no"},
    {"title":"Voucher Title","field":"voucher_title"},
    {"title":"UOM","field":"voucher_uom"}
  ];
  
  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) }

  ngOnInit() {
    this.getUOM();
  }

  getUOM(){
    this._processDataService.getData("/api/uoms").subscribe(response=>{
      if (response.status === 1) {
        this.uoms = response.data;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  getData(){
    this.voucherList= [];
    this._processDataService.getData("/api/vouchers").subscribe(response=>{
      if (response.status === 1) {
        this.voucherList = response.data;
        this.showDataModal.show(this.tableHeaders,this.voucherList);
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  getDataById(event){
    this._processDataService.getDataById("/api/vouchers/id/" + event._id).subscribe(response=>{
      if (response.status === 1) {
        this.Voucher = response.data;
        this._id=event._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/vouchers/no/" + this.Voucher.vc_no).subscribe(response=>{
      if(response.status === 1){
        this.Voucher = response.data;
        this._id = response.data._id;
        this.isNo = true;
      }else{
        this.reset();
        this.getNextNo();
      }
    })
  }

  getNextNo(){
    this._processDataService.getData("/api/nextNumber/voucher").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.Voucher.vc_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  
  save(saveForm:NgForm):void{
    debugger
    this.vehicleInfoListDto.info=this.vehicleInfoDto;
    this.vehicleLoaderListDto.info = this.loaderInfoDto;
    this.saving=true;
    let url_ = "/api/vouchers";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/vouchers/id/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
    this.getUOM();
  }
    
  create(url_: string, saveForm:NgForm): void{
    this._processDataService.create(url_,this.Voucher).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      debugger
      if (response.status == 1) {
        this.Voucher.vc_no = response.data.voucher_no + 1;
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.Voucher).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }


  
  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/vouchers/" + this._id).subscribe(result => {
          if (result.status === 1) {
            this.notify.success('Deleted Successfully.', 'Deleted!');
            this.reset();
          }else{
            this.message.fire("Error", result.message, "error");
          }
        });
      }
    });
  }

  reset():void{
    this.Voucher=new VoucherDto();
    this._id=null;
    this.isNo = false;
  }
  setTypeheadList(param){
    switch (param) {
        case 'vcModel':
        this.typeheadlist=this.vcModelList
        break;
        case 'vcType':
        this.typeheadlist=this.vcTypeList;
        break;
        case 'vcPay':
        this.typeheadlist=this.vcPaymode;
        break;
    } 
  }
}


