import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { UOMDto } from 'src/app/shared/dtos/identity/uom-dto';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-create-edit-uom',
  templateUrl: './create-edit-uom.component.html',
  styleUrls: ['./create-edit-uom.component.scss']
})
export class CreateEditUOMComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;

  Uom: UOMDto = new UOMDto();
  saving=false;
  uomList=[];
  _id=null;
  isNo = false;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Uom Title","field":"UomTitle"},
    {"title":"Uom Short","field":"UomShort"}
  ];

  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) }

  ngOnInit() {
  }

  getData(){ debugger
    this.uomList= [];
    this._processDataService.getData("/api/uoms").subscribe(response=>{


      if (response.status === 1) {
        this.uomList = response.data;
        this.showDataModal.show(this.tableHeaders,this.uomList);
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  getDataById(event){debugger
    this._processDataService.getDataById("/api/getUom/" + event._id).subscribe(response=>{

      if (response.status === 1) {
        this.Uom = response.data;
        this._id=event._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  save(CreateEditForm:NgForm):void{debugger
    this.saving=true;
    let url_ = "/api/createUom";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/updateUom/" + this._id;
    }
    this._processDataService.create(url_,this.Uom).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      this.notify.success("Saved Successfully","Success");
      CreateEditForm.resetForm();
      this._id=null;
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/Uom/no/" + this.Uom.UomOrdering).subscribe(response=>{
      if(response.status === 1){
        this.Uom = response.data;
        this._id = response.data._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
        this.reset();
      }

    })
  }
  getNextNo(){
    this._processDataService.getData("/api/nextNumber/Uom").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.Uom.UomOrdering = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/deleteUom/" + this._id).subscribe(result => {
          this.notify.success('Deleted Successfully.', 'Deleted!');
          this.Uom=new UOMDto();
          this._id=null;
        })
      }
    });
  }

  reset():void{
    this.Uom=new UOMDto();
    this._id=null;
    this.isNo = false;

  }

}
