import { CreateEditContractComponent } from './contract/create-edit-contract.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CreateEditUOMComponent } from './uom/create-edit-uom.component';
import { CreateEditShippinglineComponent } from './shippingline/create-edit-shippingline.component';
import { CreateEditTransporterComponent } from './transporter/create-edit-transporter.component';
import { CreateEditBiltyComponent } from './bilty/create-edit-bilty.component';
import { CreateEditInvoiceOpeningComponent } from './invoice-opening/create-edit-invoice-opening.component';
import { CreateEditChallanComponent } from './challan/create-edit-challan.component';
import { CreateEditBiltyExpenseComponent } from './bilty-expense/create-edit-bilty-expense.component';
import { CreateEditReceivingBillToBillComponent } from './receiving-bill-to-bill/create-edit-receiving-bill-to-bill.component';
import { CreateEditPortComponent } from './port/create-edit-port.component';
import { CreateEditShipperComponent } from './shipper/create-edit-shipper.component';
import { CreateEditBankComponent } from './bank/create-edit-bank.component';
import { CreateEditAccountTypeComponent } from './account-type/create-edit-account-type.component';
import { CreateEditInvoiceComponent } from './invoice/create-edit-invoice.component';
import { ARAgingComponent } from './ar-aging/ar-aging.component';
import { SupplierComponent } from './supplier/supplier.component';
import { CreateEditBookingVoucherComponent } from './booking-voucher/create-edit-booking-voucher.component';
import { CreateEditBookingBillTComponent } from './booking-bill-t/create-edit-booking-bill-T.component';
import { CreateEditVendorComponent } from './vendor/create-edit-vendor.component';
import { CreateEditVoucherComponent } from './voucher/create-edit-voucher.component';
import { CreateEditVehicleComponent } from './vehicle/create-edit-vehicle.component';


const routes: Routes = [
  {
    path:'',
    children:[
      {
        path:'uoms',
        component:CreateEditUOMComponent
      },
      {
        path:'ar-aging',
        component:ARAgingComponent
      },
      {
        path:'vendor',
        component:CreateEditVendorComponent
      },
      {
        path:'supplier',
        component:SupplierComponent
      },
      {
        path:'contract',
        component:CreateEditContractComponent
      },
      {
        path:'voucher',
        component:CreateEditVoucherComponent
      },
      {
        path:'bank',
        component:CreateEditBankComponent
      },
      {
        path:'account-type',
        component:CreateEditAccountTypeComponent
      },
      {
        path:'port',
        component:CreateEditPortComponent
      },
      {
        path:'shipper',
        component:CreateEditShipperComponent
      },
      {
        path:'shippingline',
        component:CreateEditShippinglineComponent
      },
      {
        path:'transporter',
        component:CreateEditTransporterComponent
      },
      {
        path:'bilty',
        component:CreateEditBiltyComponent
      },
      {
        path:'bilty-expense',
        component:CreateEditBiltyExpenseComponent
      },
      {
        path:'receiving-bill-to-bill',
        component:CreateEditReceivingBillToBillComponent
      },
      {
        path:'invoice-opening',
        component:CreateEditInvoiceOpeningComponent
      },
      {
        path:'invoice',
        component:CreateEditInvoiceComponent
      },
      {
        path:'challan',
        component:CreateEditChallanComponent
      },
      {
        path:'booking-voucher',
        component:CreateEditBookingVoucherComponent
      },
      {
        path:'booking-bill-t',
        component:CreateEditBookingBillTComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SetupRoutingModule { }
