import { IPhone } from './../../../shared/interface/identity/phone-interface';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ViewChild } from '@angular/core';
import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { StaffDto } from 'src/app/shared/dtos/identity/staff-dto';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';
import { ContactDetailComponent } from 'src/app/shared/components/contact-detail/contact-detail.component';

@Component({
  selector: 'app-create-edit-company',
  templateUrl: './create-edit-staff.component.html',
  styleUrls: ['./create-edit-staff.component.scss']
})
export class CreateEditStaffComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;
  @ViewChild('contactDetail', { static: true }) contactDetail: ContactDetailComponent;

  staff: StaffDto = new StaffDto();
  phone: IPhone = {} as IPhone;
  saving=false;
  itemList=[];
  _id=null;
  isNo = false;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Staff No","field":"stf_no"},
    {"title":"Staff Name","field":"stf_name"},
    {"title":"nick","field":"stf_nick"}
  ];

  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) }

  ngOnInit() {
    this.phone.type='Email';
    this.staff.stf_contacts = [];
  }
  getData(){
    debugger
    this.itemList= [];
    this._processDataService.getData("/api/staffs").subscribe(response=>{
      if (response.status === 1) {
        this.itemList = response.data;
        this.showDataModal.show(this.tableHeaders,this.itemList);
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataById(event){
    this._processDataService.getDataById("/api/staffs/" + event._id).subscribe(response=>{
      if (response.status === 1) {
        this.staff = response.data;
        this.contactDetail.contacts=response.data.stf_contacts;
        this._id=event._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getNextNo(){
    this._processDataService.getData("/api/nextNumber/staff").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.staff.stf_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/staffs/" + this.staff.stf_no).subscribe(response=>{
      if(response.status === 1){
        this.staff = response.data;
        this.contactDetail.contacts=response.data.stf_contacts;
      }else{
        this.notify.error('Search item not found','Not Found');
      }
    })
  }
  save(saveForm:NgForm):void{
    debugger
    this.saving=true;
    this.staff.stf_contacts=this.contactDetail.contacts;
    let url_ = "/api/staffs/";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/staffs/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }
  create(url_: string, saveForm:NgForm): void{
    this._processDataService.create(url_,this.staff).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status == 1) {
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.staff).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/staffs/" + this._id).subscribe(result => {
          if (result.status === 1) {
            this.notify.success('Deleted Successfully.', 'Deleted!');
            this.reset();
          }else{
            this.message.fire("Error", result.message, "error");
          }
        });
      }

    });
  }
  reset():void{
    this.staff=new StaffDto();
    this._id=null;
    this.isNo = false;
    this.contactDetail.contacts=[];
    this.contactDetail.contacts.push(this.phone);
  }

}


