    getData(endPoint: string | null | undefined): Observable<any> {
        let url_ = this.baseUrl + endPoint;
        url_ = url_.replace(/[?&]$/, "");

        let options_: any = {
            observe: "response",
            responseType: "blob",
            headers: new HttpHeaders({
                "Accept": "application/json",
                "Authorization":"Bearer "+this.token
            })
        };

        return this.http.request("get", url_, options_).pipe(_observableMergeMap((response_: any) => {debugger
            return this.processGetData<any>(response_);
        })).pipe(_observableCatch((response_: any) => {
            if (response_ instanceof HttpResponseBase) {
                try {
                    
                    return this.processGetData<any>(<any>response_);
                } catch (e) {
                    return <Observable<any>><any>_observableThrow(e);
                }
            } else
                return <Observable<any>><any>_observableThrow(response_);
        }));
    }

