import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ViewChild } from '@angular/core';
import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';
import { merge, Observable, OperatorFunction, Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter, finalize, map } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { ContractDto } from 'src/app/shared/dtos/identity/contract-dto';
import { IIntraContractDto } from 'src/app/shared/interface/identity/contract-interface';
import { IRateDto } from 'src/app/shared/interface/identity/rate-interface';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-create-edit-contract',
  templateUrl: './create-edit-contract.component.html',
  styleUrls: ['./create-edit-contract.component.scss']
})
export class CreateEditContractComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;
  cnModeList=['None', 'All', 'LCL', 'FCL'];
  Contract: ContractDto = new ContractDto();
  rateListDto:IIntraContractDto[]=[];
  saving=false;
  contractList=[];
  uoms=[];
  _id=null;
  isNo = false;
  tableHeaders=[
    {"title":"Id","field":"cn_no","hide":true},
    {"title":"Title","field":"cn_title"},
    {"title":"Short title","field":"cn_short"},
    {"title":"Customer","field":"cn_customer"}
  ];
  
  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) 
    this.Contract.cn_cbm=false;
  }

  ngOnInit() {
    this.Contract.cn_cbm=false;
    this.insertRateItem();
    this.getUOM();
  }
  insertRateItem(){
    var itempack={} as IRateDto; 
    this.rateListDto.push({
      rate:itempack
    });
    console.log(this.rateListDto);
  }
  //Delete dynamic grid row
  deleteRateItem(index){
    this.rateListDto.splice(index,1);
  }

  getUOM(){debugger
    this._processDataService.getData("/api/uoms").subscribe(response=>{
      if (response.status === 1) {
        this.uoms = response.data;
        this.typeheadlist=this.uoms
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  getData(){
    this.contractList= [];
    this._processDataService.getData("/api/contracts").subscribe(response=>{
      if (response.status === 1) {
        this.contractList = response.data;
        this.showDataModal.show(this.tableHeaders,this.contractList);
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  getDataById(event){
    this._processDataService.getDataById("/api/contracts/id/" + event._id).subscribe(response=>{
      if (response.status === 1) {
        this.Contract = response.data;
        this._id=event._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/contracts/no/" + this.Contract.cn_no).subscribe(response=>{
      if(response.status === 1){
        this.Contract = response.data;
        this._id = response.data._id;
        this.isNo = true;
      }else{
        this.reset();
        this.getNextNo();
      }
    })
  }

  getNextNo(){
    this._processDataService.getData("/api/nextNumber/contract").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.Contract.cn_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  save(saveForm:NgForm):void{
    this.Contract.cn_intra_ct=this.rateListDto;
    this.saving=true;
    console.log(this.Contract);
    let url_ = "/api/contracts";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/contracts/id/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
    this.getUOM();
  }

  create(url_: string, saveForm:NgForm): void{
    this._processDataService.create(url_,this.Contract).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      debugger
      if (response.status == 1) {
        this.Contract.cn_no = response.data.cn_no + 1;
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.Contract).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }


  
  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/contracts/" + this._id).subscribe(result => {
          if (result.status === 1) {
            this.notify.success('Deleted Successfully.', 'Deleted!');
            this.reset();
          }else{
            this.message.fire("Error", result.message, "error");
          }
        });
      }
    });
  }

  reset():void{
    this.Contract=new ContractDto();
    this._id=null;
    this.isNo = false;
  }
  setTypeheadList(param){
    switch (param) {
        case 'cnType':
        this.typeheadlist=this.cnModeList
        break;
    } 
  }
}


