import { IPhone } from './../../../shared/interface/identity/phone-interface';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ViewChild } from '@angular/core';
import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { OfficeDto } from 'src/app/shared/dtos/identity/office-dto';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';
import { ContactDetailComponent } from 'src/app/shared/components/contact-detail/contact-detail.component';

@Component({
  selector: 'app-create-edit-company',
  templateUrl: './create-edit-office.component.html',
  styleUrls: ['./create-edit-office.component.scss']
})
export class CreateEditOfficeComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;
  @ViewChild('contactDetail', { static: true }) contactDetail: ContactDetailComponent;

  office: OfficeDto = new OfficeDto();
  phone: IPhone = {} as IPhone;
  saving=false;
  itemList=[];
  _id=null;
  isNo=false;
  cities=[];
  city:any;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Office No","field":"off_no"},
    {"title":"Title","field":"off_title"},
    {"title":"City","field":"off_city"}
  ];

  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) }

  ngOnInit() {
    this.getLocationData();
  }
  getNextNo(){
    this._processDataService.getData("/api/nextNumber/office").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.office.off_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getData(){
    debugger
    this.itemList= [];
    this._processDataService.getData("/api/offices").subscribe(response=>{
      if (response.status === 1) {
        this.itemList = response.data;
        this.showDataModal.show(this.tableHeaders,this.itemList);
      }
    })
  }
  getDataById(event){
    this._processDataService.getDataById("/api/offices/" + event._id).subscribe(response=>{
      if (response.status === 1) {
        this.office = response.data;
        this.contactDetail.contacts=response.data.off_phones;
        this._id=event._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/offices/" + this.office.off_no).subscribe(response=>{
      if(response.status === 1){
        this.office = response.data;
        this.contactDetail.contacts=response.data.off_phones;
      }else{
        this.notify.error('Search item not found','Not Found');
        this.reset();
      }
    })
  }

  save(saveForm:NgForm):void{
    debugger
    this.office.off_city= this.city.id;
    this.office.off_phones=this.contactDetail.contacts;
    this.saving=true;
    let url_ = "/api/offices/";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/offices/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }

  create(url_: string, saveForm:NgForm): void{
    this.office.off_company= "609a786c944ebf09cc34c8f9";
    this.office.off_manager= "609a7fa71737dc36d8be4664";
    this.office.off_company= "609c01f4c645ce303c655ad2";
    
    this._processDataService.create(url_,this.office).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status == 1) {
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.office).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/offices/" + this._id).subscribe(response => {
          if (response.status === 1) {
            this.notify.success('Deleted Successfully.', 'Deleted!');
            this.reset();
          }else{
            this.message.fire("Error", response.message, "error");
          }
        });
      }
    });
  }

  reset():void{
    this.office=new OfficeDto();
    this._id=null;
    this.isNo = false;
    
  }
  setTypeheadList(param){
    switch (param) {
        case 'city':
        this.typeheadlist=this.cities;
        break;
      
    } 
  }
  getLocationData(){
    this._processDataService.getData("/api/locations/city").subscribe(response=>{
      debugger
      this.cities=[];
      if (response.status === 1) {
        response.data.forEach(loc => {
            var item = {id: loc._id,name:loc.loc_title};
            this.cities.push(item);
        });
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
}


